/* GLOBAL */
@font-face {
    font-family:SPO365Icons;
    src:url(../fonts/Office365Icons.eot);
    src:url(../fonts/Office365Icons.eot?#iefix) format("embedded-opentype"),
        url(../fonts/Office365Icons.woff) format("woff"),
        url(../fonts/Office365Icons.ttf) format("truetype"),
        url(../fonts/Office365Icons.svg#web) format("svg");
    font-style:normal;
    font-weight:400
}
* {
    box-sizing: border-box;
}
html {
    height: 100%;
}
body {
    background: #f7f7f7;
    font-family:  "Segoe UI", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 13px;
    color: #262d40;
    height: 100%;
    overflow: hidden;
}
*[class*="ion-"] {
    padding: 5px 6px;
    text-align: center;
}
*[class*="ion-"]:before {
    font-size: 18px;
}
*[class*="ion-"].ion-with-text {
    line-height: 25px;
}
*[class*="ion-"].ion-with-text:before {
    margin-right: 8px;
    font-size: 18px !important;
}
*.ion-active[class*="ion"]:before {
	color: #ffb900 !important;
}
.float-l {
    float: left;
}
.float-r {
    float: right;
}
a, a:focus, a:hover, a:active,
button, button:focus, button:hover, button:active {
    outline: 0 !important;
    border: none;
}
.small {
    font-size: 85%;
}
.large {
    font-size: 115%;
}
main {
    height: 100%;
    position: relative;
    overflow: hidden;
}
content {
    padding: 30px 15px 20px 15px;
    top: 42px;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    overflow-y: auto;
}
content p {
    line-height: 1.5em;
}
content ul > li {
    padding: 4px 0;
}
content .page-content {
    overflow: hidden;
}
.social-feed {
    width: 250px;
    margin-left: 30px;
    float: right;
    display: none;
}
.home {
    overflow: hidden;
}

/* CONTENT */
.icon-ul {
    margin: 0;
    padding-left: 10px;
    list-style-type: none;
    overflow: hidden;
}
.icon-ul li {
    min-height: 25px;
    line-height: 1.5em;
    padding: 4px 0;
    overflow: hidden;
}
.icon-ul li[class*="ion-"] {
    float: left;
    clear: left;
    margin-right: 10px;
    padding: 4px 0;
    width: 20px;
    overflow: initial;
}
.icon-ul li[class*="ion-"]:before {
    font-size: 18px;
    color: #4791d2;
}
.ion-sharepoint:before { 
    font-family: SPO365Icons; 
    content: "\e072"; 
}

/* NAV */
nav {
    height: 100%;
    width: 250px;
    float: left;
    background: #4791d2;
    display: none;
}
nav ul {
    margin: 0;
    padding: 10px 0 !important;
    list-style-type: none;
}
nav ul > li > a, nav ul > li > a:active, nav ul > li > a:focus, nav ul > li > a:visited {
    display: block;
    padding: 10px 30px;
    color: #fff;
    overflow: hidden;
    text-decoration: none;
}
nav ul > li > a:hover {
    color: #b8cedb;
    text-decoration: none;
    background: #3473a8;
}
nav .nav-icon {
    float: right;
}
nav .nav-title {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.3em;
    font-size: 14px;
}
nav ul > li > a > *[class*="ion-"] {
    padding: 0;
}
nav ul > li > a > *[class*="ion-"]:before {
    margin-left: 12px;
    text-align: center;
    font-size: 18px;
}
nav ul.sub-menu {
    background-color: #262d40;
    color: #ebecf0;
    display: none;
    padding: 2px 0 10px 0;
}
nav ul.sub-menu > li > a:hover {
    background: #7a7d8e;
    color: #ebecf0;
}
nav .open ul.sub-menu {
    display: block;
}
nav .profile {
    position: relative;
}
nav .profile-toolbar {
    position: absolute;
    width: 100%;
    background: -moz-linear-gradient(top, rgba(2,0,0,0.3333) 0%, rgba(0,0,0,0) 100%);
    background: -webkit-linear-gradient(top, rgba(2,0,0,0.3333) 0%,rgba(0,0,0,0) 100%);
    background: linear-gradient(to bottom, rgba(2,0,0,0.3333) 0%,rgba(0,0,0,0) 100%);    
}
nav .profile-toolbar .btn *[class*="ion-"]:before {
    color: #fff;
}
nav .profile-toolbar .btn:active, nav .profile-toolbar .btn:focus {
    background: transparent;
}
nav .profile .btn-float {
    position: absolute;
    top: 10px;
    right: 10px;
    visibility:hidden;
}
nav .profile .btn-float span {
    margin-right: 5px;
}
nav .profile:hover .btn-float {
    visibility: visible;
}

/* HEADER */
header {
    position: fixed;
    right: 0;
    top: 0;
    left: 0;
    z-index: 99999;
}
.appbar {
    background: #4791d2;
    color: #fdfcff;
    overflow: hidden;
    box-shadow: 0 2px 1px -1px rgba(0,0,0,0.2);
}
.appbar > div {
    font-weight: normal;
    font-size: 14px;
    cursor: pointer;
    border-right: solid 1px #5d9bd6;
}
.appbar > div:hover {
    background: #3473a8;
    color: #b8cedb;
}
.appbar > div:last-child {
    border-right: none;
}
.appbar *[class*="ion-"] {
    padding: 8px 15px;
    text-align: center;
}
.appbar *[class*="ion-"]:before {
    font-size: 24px;
}
.appbar-text {
    overflow: hidden;
    color: #c5c5c7;
}
.appbar-text input[type="text"] {
    border: none;
    padding: 11px 15px;
    font-size: 14px;
    background: #fff;
    width: 100%;
}
.appbar-text input[type="text"]:focus {
    outline: 0;
    color: #84828d;
}
.appbar-text:hover input[type="text"] {
    color: #84828d;
}
.send-feedback {
    display: none;
}

/* Social Bar */
.sidebar {
    position: absolute;
    right: 0;
    top: 42px;
    bottom: 0;
    width: 42px;
    background: #262d40;
    color: #fdfcff;
    overflow: hidden;
    z-index: 99999;
    display: none;
}
.sidebar-icon[class*="ion-"] {
    padding: 8px 12px;
    border-bottom: solid 1px #454a59;
    cursor: pointer;
}
.sidebar-header:hover .sidebar-icon[class*="ion-"] {
    background: #7a7d8e;
}
.sidebar-header:hover *[class*="ion-"]:hover {
    background: #7a7d8e;
}
.sidebar-icon[class*="ion-"]:before {
    font-size: 24px;
}
.sidebar-title {
    transform: rotate(90deg);
    font-size: 18px;
    font-weight: 500;
    padding: 8px 12px;
    cursor: pointer;
}
.sidebar-content {
    display: none;
    background: #fff;
    top: 41px;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
}
.sidebar.open {
    width: 450px;
    box-shadow: -2px 0px 2px -1px rgba(0,0,0,0.2);
}
.sidebar.open .sidebar-content {
    display: block;
}
.sidebar.open .sidebar-header {
    overflow: hidden;
}
.sidebar.open .sidebar-icon[class*="ion-"] {
    float: left;
    border-right: solid 1px #454a59;
    border-bottom: none;
}
.sidebar.open .sidebar-title {
    transform: initial;
    overflow: hidden;
}

/* BUTTON */
button:active, .btn:active {
    outline: none;
    border: none;
}
button:focus, .btn:focus {
    outline: 0;
}
.btn {
    background: #4791d2;
    border-radius: 0;
    box-shadow: 0 2px 1px 0px rgba(0,0,0,0.2);
    color: #fff;
    padding: 7px 15px;
    border: none;
    font-size: 14px;
}
.btn-float *[class*="ion-"] {
    display: inline;
    padding: 0;
}
.btn-flat *[class*="ion-"] {
    padding: 0;
    line-height: 18px;
}
.btn-flat *[class*="ion-"]:before {
    font-size: 24px !important;
    color: #4791d2;
}
.btn:hover {
    color: #b8cedb;
}
.btn:active, .btn:focus {
    background: #3473a8;
    color: #b8cedb;
}
.btn-flat {
    background: transparent;
    color: #4791d2;
    text-transform: uppercase;
    font-size: 13px;
    box-shadow: none;
    line-height: 24px;
}
.btn-flat:hover {
    color: #4791d2;
}
.btn-flat:active, .btn-flat.btn-active {
    background: #eaeaea;
    color: #4791d2;
    box-shadow: none;
}
.btn-flat:focus {
    background: #e5e5e5;
    color: #4791d2;
    box-shadow: none;
}
.btn-flat.disabled, .btn-flat.disabled:hover, .btn-flat.disabled:active, .btn-flat.disabled:focus {
    color: rgba(0,0,0,0.26);
}
.btn-float {
    border-radius: 20px;
    padding: 7px 17px;
}

/* TABLES */
.table { 
    border-spacing: 5px;
    width: 100%;
    border-top: solid 1px #e9e9e9;
    border-left: solid 1px #dfdfdf;
    border-right: solid 1px #dfdfdf;
    box-shadow: 0 2px 1px -1px rgba(0,0,0,0.2);
    text-align: left;
}
.table > tbody > tr {
    vertical-align: middle;
}
.table > tbody > tr > th, .table > tbody > tr > td {
    padding: 12px 15px;
    background: #fff;
    border-bottom: solid 1px #e9e9e9;
}
.table > tbody > tr > th {
    color: #505358;
    font-weight: 300;
    padding: 15px;
    vertical-align: middle;
}
.table > tbody > tr > td {
    font-weight: normal;
    color: #262d40;
    vertical-align: middle;
}
.table > tbody > tr:hover > td {
    background: #eee;
}
.table > tbody > tr > th.sort {
    font-weight: normal;
}
.table > tbody > tr > th.sort *[class*="ion-"] {
    padding: 0;
}
.table > tbody > tr > th.sort *[class*="ion-"]:before {
    margin-right: 5px;
}
.table > tfoot > tr > td {
    text-align: center;
    border-bottom: none;
    background: #fff;
}
.table *[class*="ion-"] {
    text-align: center;
    float: left;
    color: #262d40;
    padding: 0;
    line-height: 0;
}
.table *[class*="ion-"]:before {
    font-size: 18px;
}
.table > tbody > tr.selected .ion-android-checkbox-outline-blank:before {
    content: "\f374";
    color: #4791d2;
}
.table > tbody > tr.selected > td {
    background: #f5f5f5;
}
.table > tbody > tr > .td-icon, .table > tbody > tr > th-icon {
    width: 18px;
}
.context-menu {
    float: right !important;
    margin-left: 12px;
    position: relative;
    cursor: pointer;
}
.context-menu .menu {
    top: 0;
}
.card .table {
    box-shadow: none;
    border: none;
    border-top: solid 1px #e9e9e9;
    margin-bottom: 0;
}
.card .table > tr:last-child > td {
    border-bottom: none;
}

/* TOOLBAR */
.toolbar {
    font-size: 13px;
    background: #e9e9e9;
    border-top: solid 1px #dfdfdf;
    border-left: solid 1px #dfdfdf;
    border-right: solid 1px #dfdfdf;
}
.toolbar:after {
     visibility: hidden;
     display: block;
     font-size: 0;
     content: " ";
     clear: both;
     height: 0;
}
.list .toolbar {
    border-bottom: solid 1px #dfdfdf;
    margin-bottom: 20px;
    text-align: left;
}
.toolbar-filters > ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    text-align: left;
}
.toolbar-filters > ul > li {
    display: block;
    float: left;
    font-size: 13px;
    padding: 11px 10px;
    line-height: 22px;
    border-right: solid 1px #dfdfdf;
    display: none;
    cursor: pointer;
    position: relative;
}
.toolbar-filters > ul > li:hover {
    background: #f2f2f2;
}
.toolbar-filters > ul > li:first-child {
    padding: 11px 15px;
    display: block;
    cursor: default;
}
.toolbar-filters > ul > li:first-child:hover {
    background: transparent;
}
.toolbar-filters > ul > li:last-child {
    margin-right: 0;
}
.toolbar-filters > ul > li.toolbar-search {
    display: block;
    border-right: none;
    padding: 0;
    margin-right: 0;
    overflow: hidden;
    float: none;
}
.toolbar-filters > ul > li.toolbar-search:hover {
    background: transparent;
}
.card .toolbar {
	border-left: none;
	border-right: none;
}
.card .chip-container {
	margin-bottom: 0;
}
.toolbar-filters *[class*="ion-"]:before {
    font-size: 14px;
    color: #262d40;
}
.toolbar-filters > ul > li[class*="ion-"]:first-child:before {
    font-size: 18px;
    color: #262d40;
}
.toolbar-input {
    overflow: hidden;
}
.toolbar-search input[type="text"] {
    border: none;
    padding: 11px 15px 11px 0;
    font-size: 14px;
    background: #e9e9e9;
    width: 100%;
}
.toolbar-search input[type="text"]:focus {
    outline: 0;
    color: #84828d;
}
.toolbar-search:hover input[type="text"] {
    color: #84828d;
}
.toolbar-search *[class*="ion-"] {
    float: left;
    padding: 11px;
}
.toolbar-search *[class*="ion-"]:before {
    font-size: 18px;
    color: #262d40;
}

/* CARDS */
.card {
    border-top: solid 1px #e9e9e9;
    border-left: solid 1px #dfdfdf;
    border-right: solid 1px #dfdfdf;
    border-bottom: solid 1px #e9e9e9;
    box-shadow: 0 2px 1px -1px rgba(0,0,0,0.2);
    border-radius: 4px;
    margin-bottom: 30px;
}
.card > a, .card > a:active, .card > a:link, .card > a:hover, .card > a:visited {
    color: #262d40;
    text-decoration: none;
}
.card-header {
    padding: 12px 15px;
    background-color: #fff;
    overflow: hidden;
}
.card-header .card-title *[class*="ion-"] {
    float: left;
    margin-right: 12px;
    padding: 0;
}
.card-header .card-title *[class*="ion-"]:before {
    font-size: 24px;
    color: #4791d2;
}
.card-title {
    font-family: "Segoe UI", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 24px;    
    text-transform: capitalize;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.3em;
}
.card-title.small {
    font-size: 18px;    
}
.card-title-with-icon {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.card-actions {
    overflow: hidden;
    background: #fff;
    color: #4791d2;
    padding: 5px 5px 5px 10px;
	text-align: center;
}
.card-actions button {
    background: transparent;
}
.card-body {
    background: #fff;
}
.card-content {
    padding: 15px;
    text-align: left;
}
.card-image > img {
    width: 100%;
}
.square {
    overflow: hidden;
    display: block;
    width: 100%;
    padding-top: 100%;
    position: relative;
}
.square > img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.nomargin {
    padding: 0 !important;
    margin: 0 !important;
}

/* LIST */
.list {
    position: relative;
    overflow: hidden;
}
.list-item > *[class*="ion-"] {
    padding: 0 10px;
    text-align: center;
    float: left;
}
.list-item > *[class*="ion-"]:before {
    font-size: 18px;
    color: #4791d2;
    line-height: 24px;
}
.list-item > .list-icon {
    float: left;
    line-height: 18px;
    margin-right: 10px;
}
.list-item > .list-icon > .thumbnail-wrapper {
    width: 18px;
    height: 18px;
}
.list-item > .list-icon > img {
    height: 18px;
}
.list-item > .list-icon[class*="ion-"] {
    padding-left: 0;
} 
.list-item > .list-action {
    color: rgba(38,45,64,0.5);
}
.list-item > .list-action[class*="ion-"] {
    float: right;
    padding-right: 0;
	padding-left: 0;
	cursor: pointer;
}
.list-item > .list-action[class*="ion-"]:before {
    font-size: 18px;
    color: rgba(38,45,64,0.5);
    line-height: 24px;
}
.list-item > .list-action[class*="ion-"]:hover:before {
    color: rgba(38,45,64,1);
}
.list-item {
    margin-bottom: 10px;
    overflow: hidden;
	cursor: pointer;
}
.list-body {
    text-align: left;
    overflow: hidden;
}
.list-body > * {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    line-height: 1.3em;
    font-size: 14px;
}
.list-title {
    font-size: 18px;
    font-weight: normal;
}
.list-line {
    color: rgba(38,45,64,0.5);
}
.list-line *[class*="ion-"] {
    padding: 0;
    display: inline;
    margin-right: 5px;
}
.list-line *[class*="ion-"]:before {
    font-size: 14px;
    color: rgba(38,45,64,0.5);
}
.list-image {
	float: left;
	text-align: center;
	margin-right: 12px;
	position: relative;
}
.list-image {
	width: 50px;
	height: 50px;
}
.list-image .thumbnail-wrapper > *[class*="ion-"] {
	padding-bottom: 0;
	padding-left: 0;
	padding-right: 0;
	padding-top: 14px;
}
.list-image .thumbnail-wrapper.bordered > *[class*="ion-"] {
    padding-top: 9px;
}
.list-image .thumbnail-wrapper {
	padding-top: 50px;
}
.list-image .thumbnail-wrapper.bordered {
	padding-top: 42px;
}
.list-image .thumbnail-wrapper > *[class*="ion-"]:before {
	color: #fff;
	font-size: 24px;
    line-height: 24px;
}
.list-item.single > .list-action[class*="ion-"]:before,
.list-item.single > .list-body .list-title {
	line-height: 46px;
}
.avatar {
    text-align: center;
    float: left;
    margin-right: 12px;
}
.avatar .thumbnail-wrapper {
    width: 50px;
    height: 50px;
}
.thumbnail-wrapper {
    display: inline-block;
    overflow: hidden;
    float: left;
}
.thumbnail-wrapper.bordered {
    border-width: 4px;
    border-style: solid;
}
.thumbnail-wrapper.circular {
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
}
.b-white {
    border-color: #fff;
}
.b-white:hover {
    border-color: #fff;
}
.thumbnail-wrapper > * {
    vertical-align: middle;
    width: 100%;
    height: 100%;
    text-align: center;
}
.thumbnail-wrapper > img {
    height: 100%;
    width: auto;
    margin-left: auto;
    margin-right: auto;
}
.thumbnail-wrapper.tall > img {
    width: 100%;
    height: auto;
}
.thumbnail-div {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50% 50%;
}
.card .list {
    border-top: solid 1px #e9e9e9;
}
.card .list-item {
    background: #fff;
    border-bottom: solid 1px #e9e9e9;
    padding: 12px;
    margin-bottom: 0;
}
.card .list-item:hover {
    cursor: pointer;
}
@media (max-width: 767px) {
    .card div.col-xs-6.list-item {
        border-right: solid 1px #e9e9e9;
    }   
    .card div.col-xs-6.list-item:nth-of-type(2n) {
        border-right: none;
    }         
}
@media (min-width: 768px) and (max-width: 991px) {
    .card div.col-sm-6.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-sm-6.list-item:nth-of-type(2n) {
        border-right: none;
    }  
}
@media (min-width: 992px) and (max-width: 1199px) {
    .card div.col-md-6.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-md-6.list-item:nth-of-type(2n) {
        border-right: none;
    }  
    .card div.col-md-4.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-md-4.list-item:nth-of-type(3n) {
        border-right: none;
    }     
    .card div.col-md-3.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-md-3.list-item:nth-of-type(4n) {
        border-right: none;
    }        
}
@media (min-width: 1200px) {
    .card div.col-lg-6.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-lg-6.list-item:nth-of-type(2n) {
        border-right: none;
    }  
    .card div.col-lg-4.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-lg-4.list-item:nth-of-type(3n) {
        border-right: none;
    }  
    .card div.col-lg-3.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-lg-3.list-item:nth-of-type(4n) {
        border-right: none;
    }  
    .card div.col-lg-2.list-item {
        border-right: solid 1px #e9e9e9;
    }
    .card div.col-lg-2.list-item:nth-of-type(6n) {
        border-right: none;
    }  
    .card div.col-lg-1.list-item {
        border-right: solid 1px #e9e9e9;
    }
