/* CHIPS */
.chip-container {
    margin-bottom: 20px;
    overflow: hidden;
	padding-left: 8px;
}
.chip {
    background: rgba(38,45,64,0.25);
    border-radius: 20px;
    padding: 7px 12px;
    color: #fff;
    line-height: 18px;
    display: inline-block;
    position: relative;
    margin-bottom: 1px;
}
.chip:hover {
    background: #262d40;
