/* Menus */
.menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    -webkit-box-shadow: 0 0 6px -1px rgba(0,0,0,.16);
    box-shadow: 0 0 6px -1px rgba(0,0,0,.16);  
    border: solid 1px #dfdfdf;
    border-top: none;    
    min-width: 100px;
    max-width: 250px;
    width: auto;      
    background: #fff;    
}
.menu-header {
    position: relative;
    overflow: hidden;
    box-shadow: 0 2px 1px -1px rgba(0,0,0,0.2);
    background: #fff;  
    z-index: 1;
}
.menu .scroll-content {
    max-height: 300px;
}
