    padding-right: 0;
}
.menu .list {
    padding: 10px 0;
    overflow: visible;
}
.menu .list > .list-action {
    float: right;
}
.menu .list > .list-item {
    cursor: pointer;
    background: transparent;
    padding: 8px 12px;
    margin-bottom: 0;
    position: relative;
    min-width: 100%;
    overflow: visible;
}
.menu .list > .list-item .list-action ~ .list-body {
    overflow: visible;
    margin-right: 28px;
}
.menu .list > .list-item .list-icon ~ .list-body {
    overflow: visible;
    margin-left: 28px;
}
.menu .list > .list-item .list-title {
    font-size: 16px;
}
.menu .list > .list-item:hover,
.menu .list > .list-item:focus,
.menu .list > .list-item:active {
    background: #f2f2f2;
}
